import { removeCookie } from "../services/cookieService";
import { displayPage } from "./changeVisibility";


export  function CloseAllPages(){
    //In this part you need add all new pages!!!
    displayPage("none","CompanyDashBoard")
    displayPage("none","App")
    displayPage("none","login")
    displayPage("none","GeneralPages")
    displayPage("none","Lobby")
    displayPage("none","CycleInventory")
    displayPage("none","CycleInventoryGraph")
    displayPage("none","KPIGraph")
    displayPage("none","Inventory")
}

export  function OpenCompanyDashBoard(){
    CloseAllPages()
    displayPage("","App")
    displayPage("","CompanyDashBoard")
}

export  function OpenLobby(){
   CloseAllPages()
    displayPage("","App")
    displayPage("","GeneralPages")
    displayPage("","Lobby")
}

export async  function OpenCycleInventory(){
    var button =document.getElementById('actionatorCycleInventory')
    await button.click()
    CloseAllPages()
    displayPage("","App")
    displayPage("","GeneralPages")
    displayPage("","CycleInventory")
}
export  function OpenCycleInventoryGraph(){
    CloseAllPages()
    displayPage("","App")
    displayPage("","GeneralPages")
    displayPage("","CycleInventoryGraph")
}

export async  function OpenKPIGraph(){
    var button =document.getElementById('actionatorKPI')
    await button.click()
    CloseAllPages()
    displayPage("","App")
    displayPage("","GeneralPages")
    displayPage("","KPIGraph")
}

export function CloseCompanySession(){
    removeCookie('Company')
    OpenCompanyDashBoard()

}


export  function OpenInventory(){
    CloseAllPages()
    displayPage("","App")
    displayPage("","GeneralPages")
    displayPage("","Inventory")
}


export  function OpenLogin(){
    CloseAllPages()
    displayPage("","login")
}

export default{OpenLogin,CloseAllPages,OpenCycleInventory,OpenLobby,OpenCompanyDashBoard,OpenCycleInventoryGraph,CloseCompanySession}